      selectLang(indoLang)
      store.commit('changslang', 'id')
    } else if (slang.value === 'en') {
      selectLang(engLang)
      store.commit('changslang', 'en')
    } else if (slang.value === 'zh') {
      selectLang(chnLang)
      store.commit('changslang', 'zh')
    }

    if (lang === 'id') {
      selectLang(indoLang)
    } else if (lang === 'en') {
      selectLang(engLang)
    } else if (lang === 'zh') {
      selectLang(chnLang)
    }

    return { showLang, selectLang, chnLang, engLang, indoLang, slang }
  }
}
</script>

